body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face { 
  font-family: 'yg-jalnan'; 
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_four@1.2/JalnanOTF00.woff') 
  format('woff'); 
  font-weight: normal; 
  font-style: normal; 
}
.app {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url('https://cdn.pixabay.com/photo/2018/01/14/23/12/nature-3082832_960_720.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.7;
}
.watch__text {
  color: white;
  font-size: 75px;
  font-family: 'yg-jalnan';
}
.menu {
  width: 100%;
  height: 50px;
  background-color: transparent;
  position: fixed;
  z-index: 999;
  top: 0;
  display: flex;
  justify-content: flex-end;
}

.menu__button:hover {
  cursor: pointer;
}

.menu__button {
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 10px;
  background-color: transparent;
  color: #fff;
  border: 2px solid #fff;
  border-radius: 5px;
  font-size: 15px;
  font-family: 'yg-jalnan';
  outline: none;
}

.menu__button__active {
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 10px;
  background-color: transparent;
  color: red;
  border: 2px solid red;
  border-radius: 5px;
  font-size: 15px;
  font-family: 'yg-jalnan';
  outline: none;
}

.menu__select {
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 5px;
  background-color: transparent;
  color: #fff;
  border: 2px solid #fff;
  border-radius: 5px;
  font-family: 'yg-jalnan';
  outline: none;
}

.menu__select:hover {
  cursor: pointer;
}

.menu__select option {
  background-color: #a4a4a4;
  color: #fff;
  font-family: 'yg-jalnan';
}

.menu__select__active {
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 5px;
  background-color: transparent;
  color: red;
  border: 2px solid red;
  border-radius: 5px;
  font-family: 'yg-jalnan';
}
